checkTrigger = function(i) {
  var right, triggerContainerWidth, triggerCoords;
  if (i != null) {
    if (matchTrigger) {
      style = `width: ${width};top: ${i.clientHeight + 4}px; width: ${i.clientWidth + 2}px`;
