<script lang="coffeescript">var destroy, dispatch, forceSync, importData, notificationPermssionRequest, replicationDb, replicationEnabled, replicationRequiresAuthentication, resource, restore, saveAs, subscribe, syncing, toggleSync, updateDb, updatePassword, updateUser, web;

import {
  isSyncing
} from "../../helpers/stores.js";

import PageContent from "../_Common/PageContent.svelte";

import PageHeader from "../_Common/PageHeader.svelte";

import FileSaver from "file-saver";

import md5 from "md5";

import Project from '../../helpers/project.js';

import Checkbox from '../../components/Form/Checkbox/Checkbox.svelte';

import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte';

import InlinePassword from '../../components/Form/InlineInput/InlinePassword.svelte';

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

resource = new Project();

replicationEnabled = localStorage.getItem("sync");

replicationDb = localStorage.getItem("remote_db");

replicationRequiresAuthentication = localStorage.getItem("sync_authentication") || "false";

syncing = void 0;

subscribe = isSyncing.subscribe(function(val) {
  return syncing = val;
});

toggleSync = function() {
  localStorage.setItem("sync", !(replicationEnabled === 'true'));
  return replicationEnabled = localStorage.getItem("sync");
};

web = !window.electron;

updateDb = function(e) {
  return console.log(e);
};

updateUser = function(e) {
  return console.log(e);
};

updatePassword = function(e) {
  return console.log(e);
};

saveAs = function() {
  var projectLoadedScb;
  projectLoadedScb = function(projects) {
    var blob, jsonString, list;
    list = projects;
    jsonString = JSON.stringify(list, null, 2);
    blob = new Blob([jsonString], {
      type: "text/plain;charset=utf-8"
    });
    // note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
    // use a different handler for electron
    return FileSaver.saveAs(blob, `braintapper-archive-${Date.create().toTimestampId()}--${md5(jsonString)}.json`);
  };
  return resource.select(projectLoadedScb, null, null);
};

forceSync = function() {
  console.log("force sync");
  window.pouch.sync_pending = true;
  return window.pouch.sync();
};

importData = function() {};

restore = function() {};

destroy = function() {};

notificationPermssionRequest = function() {
  return Notification.requestPermission().then(function(permission) {
    var notification;
    // If the user accepts, let''s create a notification
    if (permission === 'granted') {
      notification = new Notification('Hi there!');
    } else {
      console.log("not granted");
    }
  });
};

$: disableReplication = (replicationEnabled != 'true');
</script>
<style lang="sass">article {
  margin: 0px 52px; }

button [disabled] {
  background: var(--lightest-gray) !important; }

/*# sourceMappingURL=Sync.svelte.css.map */</style>

<svelte:head>
  <title>Sync</title>
</svelte:head>
<PageContent>
  <a name="top"></a>
  <PageHeader>Sync</PageHeader>
  <article type="content">

    <div>
        <button type="button" primary on:click={forceSync} disabled={disableReplication}>Force Sync</button>
    </div>



    <h2> Warning, Not All Features Below May Not Be Functional</h2>
    <h3>Synchronized Database</h3>
    <div>
      This application can sync with any CouchDB compatible database.
    </div>
    <div>
      <field>
        <Checkbox checked={replicationEnabled} key="sync" label="Enable Synchronization with Remote Database" on:change={toggleSync}/>
      </field>
      <field>
        <label type="field">Sync Interval</label>
        <input type="text"/>
      </field>
      <field>
        <label for="field">Database URL</label>
        <InlineInput bind:value={replicationDb} on:update={updateDb} let:label>
          <span ellipsis>{replicationDb}</span>
        </InlineInput>
      </field>

      <field>
        <label type="field">User Name</label>
        <input type="text"/>
      </field>
      <field>
        <label type="field">Password</label>
        <input type="password"/>
      </field>

      <field>
        <button type="button" primary>Test</button>
      </field>
      <div>
        <h4>Warnings</h4>
        <ul>
          {#if web}
          <li>
            If you do not wanted to be prompted every time you sync, you need to upgrade to the desktop application.
          </li>
          <li>
            If you choose to cheat by embedding your credentials in the URL, you do so at your own peril. The URL is stored in plaintext in your browser's LocalStorage.
          </li>
          {/if}
          <li>
            If your URL starts with <code>http://</code> and not <code>https://</code> your data will be transmitted unencrypted. Do not use <code>http://</code> if you can avoid it.
          </li>
        </ul>
      </div>
    </div>

    <div>!!disableReplication: {JSON.stringify(disableReplication)} :: disableReplication</div>




  </article>

</PageContent>
