<script lang="coffeescript">var current, currentCalendar, currentCalendarHeader, currentMonth, dispatch, displayDate, eventsForDay, generate, next, nextYear, previous, previousYear, setDay, today, tomorrow, weeks, yesterday;

export var value = null;

export var events = [];

import {
  Row,
  RowCell
} from "../../Flex";

import {
  createEventDispatcher
} from 'svelte';

dispatch = createEventDispatcher();

weeks = [];

console.log(events);

eventsForDay = function(day) {
  if (events.length === 0) {
    return [];
  } else {
    return events.filter(function(event) {
      return Date.create(event.start).format("{MM}{dd}") === Date.create(day).format("{MM}{dd}");
    });
  }
};

currentCalendar = [];

currentMonth = null;

currentCalendarHeader = null;

generate = function() {
  var calendarEnd, calendarStart, count, currentWeek, results, runningDate;
  weeks = [];
  if (typeof displayDate !== "undefined" && displayDate !== null) {
    console.log("displayDate");
    console.log(displayDate);
    currentCalendarHeader = displayDate.format("{Month} {yyyy}");
    currentMonth = displayDate.format("{M}");
    calendarStart = displayDate.clone().beginningOfMonth().beginningOfWeek();
    calendarEnd = displayDate.clone().endOfMonth().endOfWeek();
    runningDate = calendarStart.clone();
    currentWeek = [];
    count = 0;
    results = [];
    while (runningDate.isBetween(calendarStart, calendarEnd)) {
      currentWeek.push(runningDate.clone());
      runningDate.addDays(1);
      count++;
      if (count === 7) {
        weeks.push(currentWeek);
        currentWeek = [];
        results.push(count = 0);
      } else {
        results.push(void 0);
      }
    }
    return results;
  }
};

displayDate = void 0;

current = function() {
  console.log("Value");
  console.log(value);
  if (value != null) {
    displayDate = Date.create(value);
  } else {
    displayDate = Date.create();
  }
  return generate();
};

next = function() {
  displayDate.addMonths(1);
  generate();
  return console.log(`value ${value}`);
};

previous = function() {
  displayDate.addMonths(-1);
  generate();
  return console.log(`value ${value}`);
};

nextYear = function() {
  displayDate.addYears(1);
  generate();
  return console.log(`value ${value}`);
};

previousYear = function() {
  displayDate.addYears(-1);
  generate();
  return console.log(`value ${value}`);
};

today = function() {
  setDay(Date.create());
  return console.log("today");
};

yesterday = function() {
  return setDay(Date.create("yesterday"));
};

tomorrow = function() {
  return setDay(Date.create("tomorrow"));
};

current();

setDay = function(day) {
  console.log(`CALENDAR SETDAY ${day}`);
  return dispatch("change", day);
};
</script>





<div container>
  <table calendar>
    <thead>
      <tr>
        <td colspan=7>
          <Row>
            <RowCell style="width: 33%">
              <button type="button" on:click={yesterday} dayshortcut>
                Yesterday
              </button>
            </RowCell>
            <RowCell flex="flex" style="width: 33%">
              <button type="button" on:click={today} dayshortcut>
                Today
              </button>
              </RowCell>
            <RowCell style="width: 33%">
              <button type="button" on:click={tomorrow} dayshortcut>
                Tomorrow
              </button>

            </RowCell>
          </Row>
        </td>
      </tr>
      <tr>
        <td colspan=7>
          <Row>
            <RowCell>
              <button type="button" icon on:click={previousYear}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M6,18V6H8V18H6M9.5,12L18,6V18L9.5,12Z"/></svg>
              </button>

              <button type="button" icon on:click={previous}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12l4.58-4.59z" class="fill"/></svg>
              </button>
            </RowCell>
            <RowCell flex="flex"><div monthname>{currentCalendarHeader}</div></RowCell>
            <RowCell>
              <button type="button" icon on:click={next}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z" class="fill"/></svg>
              </button>
              <button type="button" icon on:click={nextYear}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M16,18H18V6H16M6,18L14.5,12L6,6V18Z"/></svg>
              </button>
            </RowCell>
          </Row>
        </td>
      </tr>
      <tr weekdays>
        <td>S</td>
        <td>M</td>
        <td>T</td>
        <td>W</td>
        <td>T</td>
        <td>F</td>
        <td>S</td>
      </tr>
    </thead>
    <tbody>
      {#each weeks as week, weekIndex}
        <tr>

          {#each week as day, dayindex}
            {#if (parseInt(day.format("{M}")) != parseInt(currentMonth)) }
              {#if (weekIndex < 1) }
                <td month="previous" events={eventsForDay(day).length} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
              {:else}
                <td month="next" events={eventsForDay(day).length} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
              {/if}
            {:else}
              <td month="current" events={eventsForDay(day).length} today={day.isToday()} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
            {/if}
          {/each}
        </tr>
      {/each}
    </tbody>
    <tfoot>

    </tfoot>

  </table>
</div>

