<script lang="coffeescript">var deferredUpdate, destroy, expandState, getProject, item, jump, list, resource, setSubtitle, setTitle, subresource, updateItem, updateProject;

import {
  push
} from 'svelte-spa-router';

import Resource from '../../helpers/project.js';

import SubDocument from "../../helpers/sub_document.js";

import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte';

import DeleteConfirmation from '../../components/Overlay/Modal/DeleteConfirmation.svelte';

import {
  Row,
  RowCell
} from "../../components/Flex";

import FavoritesMenu from "../_Common/Menus/Favorites.svelte";

import PageHeader from '../_Common/PageHeader.svelte';

import Description from '../_Common/Sections/Description.svelte';

import Checklist from '../_Common/Sections/Checklist.svelte';

import Time from '../_Common/Sections/Time.svelte';

import Comments from '../_Common/Sections/Comments.svelte';

import Clips from '../_Common/Sections/ClipList.svelte';

import SubDocument_Settings from '../_Common/Sections/SubDocument_Settings.svelte';

import Progress from '../_Common/Popovers/Progress.svelte';

import DueDatePopover from '../_Common/Popovers/DueDate.svelte';

import EstimatePopover from '../_Common/Popovers/Time.svelte';

// Props
export var params = void 0;

export var project = void 0;

export var collection = void 0;

resource = new Resource;

subresource = new SubDocument;

item = void 0;

list = [];

getProject = function() {
  var scb;
  scb = function(doc) {
    project = doc;
    console.log(subresource);
    console.log("GET RPOJECT SCB");
    item = subresource.upgradeModel(project[collection].find({
      id: params.id
    }));
    return console.log(item);
  };
  // unless item? 404
  return resource.selectOne(params.project_id, scb, null, null);
};

getProject();

updateProject = function() {
  var ecb, scb;
  scb = function(updated) {};
  //console.log "updated"
  ecb = function(err) {
    console.error(err);
    return getProject();
  };
  return resource.update(project, scb, ecb, null);
};

updateItem = function(e) {
  //console.log e.detail
  Object.merge(item, e.detail);
  updateProject();
  return item = item; // force reaction
};

deferredUpdate = updateProject.debounce(500);

destroy = function() {
  var ecb, scb;
  project[collection].remove({
    id: params.id
  });
  scb = function(updated) {
    return push(`/projects/${project.id // go back to project page
}`);
  };
  ecb = function(err) {
    console.log("udpate err");
    console.log(err);
    return getProject();
  };
  return resource.update(project, scb, ecb, null);
};

jump = function(anchorName) {
  var elem, scrollContainer;
  elem = u(`[name=${anchorName}]`);
  scrollContainer = u("body > main > section").first();
  return scrollContainer.scrollTop = elem.first().offsetTop - 40;
};

expandState = function(expanded) {
  return ["details", "description", "checklist", "time_entries", "comments", "clips"].forEach(function(section) {
    return item.settings.sections[section].expanded = expanded;
  });
};

setTitle = function(val) {
  if (val != null) {
    return `${project.name} > ${val.name}`;
  } else {
    return "";
  }
};

setSubtitle = function(val) {
  switch (val) {
    case "tasks":
      return "Task";
    case "assignments":
      return "Assignments";
    case "documents":
      return "Documents";
  }
};

//}
$: title = setTitle(item);

$: subtitle = setSubtitle(collection);
</script>

<svelte:head>
  <title>{title}</title>
</svelte:head>

<div>
   {#if item}
      <a name="top"></a>
      <PageHeader >
        <h6 slot="subtitle">
          <Row>
            <RowCell>
              <FavoritesMenu/>&nbsp;&gt;&nbsp;
            </RowCell>
            <RowCell>
              <a href="#/projects/{project.id}"><em>{project.name}</em></a>&nbsp;&gt;&nbsp;
            </RowCell>
            <RowCell><em>{collection.titleize().singularize()}</em></RowCell>
          </Row>
        </h6>
        <div slot="icon"><Progress bind:value={item.progress} {item} on:update={updateItem} size={36}/></div>
        <InlineInput bind:value={item.name} on:update={deferredUpdate} style="margin-left: -2px"/>
        <div slot="secondary-info">
          <div layout="row">



            <!-- due date menu -->
            <div flex="initial" present={item.due_date != null}>
              <DueDatePopover item={project} value={item.due_date} on:update={updateItem} on:open on:close isListItem={false}/>
            </div>
            <div flex="initial" present={(item.estimate || 0) > 0} style="padding-left: 8px">
              <EstimatePopover item={item}  on:update={updateItem} on:open on:close isListItem={false}/>
            </div>



          </div>
        </div>
      </PageHeader>


      <a name="about"></a>
      <Description bind:item={item} bind:open={item.settings.sections.about.expanded} on:update={deferredUpdate}/>

      <a name="checklist"></a>
      <Checklist {project} bind:item={item} bind:open={item.settings.sections.checklist.expanded} on:update={deferredUpdate}/>

      <a name="clips"></a>
      <Clips bind:item={item} bind:open={item.settings.sections.clips.expanded} on:update={deferredUpdate}/>

      <a name="time"></a>
      <Time {project} bind:item={item} bind:open={item.settings.sections.time_entries.expanded} on:update={deferredUpdate}/>

      <a name="comments"></a>
      <Comments bind:item={item} bind:open={item.settings.sections.comments.expanded} on:update={deferredUpdate}/>

      <a name="settings"></a>
      <SubDocument_Settings bind:item={item} open={true} on:update={deferredUpdate} on:destroy={destroy}/>
    {/if}
  </div>



<style lang="sass">h6 {
  text-transform: uppercase;
  margin-bottom: -4px;
  color: var(--gray);
  letter-spacing: 0.75px; }
  h6 div em {
    color: var(--black);
    font-style: normal;
    font-weight: var(--font-bold); }

/*# sourceMappingURL=ProjectChild.svelte.css.map */</style>
