<script lang="coffeescript">// Lib
var add, copy, deleteConfirmationCancel, destroy, dispatch, dropEvent, getWidth, message, rowEl, showDeleteConfirmation, showDeleteModal, showToast, sort, style, subDocument, toggleVisibility, update, updateAttribute, updateName;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import Clip from "../../../helpers/clip.js";

import Toast from "../../../components/Toast/Toast.svelte";

import Indent from "../Indent.svelte";

import DraggableItem from "../../_Common/Lists/Nestable/DraggableIndentableItem.svelte";

import DroppableDivider from "../../_Common/Lists/Nestable/DroppableDivider.svelte";

import QuickEntry from "../../_Common/QuickEntry/Clip.svelte";

import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte';

import HierarchicalList from "../../../helpers/hierarchical_list.js";

import Section from "./_Section.svelte";

import Menu from '../Menus/Clip.svelte';

// Props
export var item = void 0;

export var collection = "clips";

export var listId = uuid();

export var sameListOnly = true;

subDocument = new Clip;

dispatch = createEventDispatcher();

message = "";

showToast = false;

style = ":hover {font-weight: bold}";

copy = function(e) {
  return console.log("COPY CLICK");
};

copy = function(val) {
  var dummy;
  console.log("copy clicked");
  dummy = document.createElement("input");
  document.body.appendChild(dummy);
  dummy.setAttribute("id", `clip-${val.id}`);
  document.getElementById(`clip-${val.id}`).value = val.content;
  dummy.select();
  document.execCommand("copy");
  document.body.removeChild(dummy);
  // toast it!
  message = `Copied value for item named <b>${val.name.truncateOnWord(255)}</b> to clipboard`;
  return showToast = true;
};

toggleVisibility = function(val) {
  var thisItem;
  console.log(val);
  thisItem = item.clips.find({
    id: val.id
  });
  thisItem.is_visible = !thisItem.is_visible;
  item[collection] = item[collection];
  return dispatch("update");
};

showDeleteConfirmation = false;

showDeleteModal = function() {
  return showDeleteConfirmation = true;
};

deleteConfirmationCancel = function() {
  return showDeleteConfirmation = false;
};

destroy = function(e) {
  console.log("ClipList.destroy");
  console.log(e.detail);
  item[collection].remove({
    id: e.detail.id
  });
  item[collection] = item[collection];
  dispatch("update");
  return showDeleteConfirmation = false;
};

updateName = function(e) {
  var change;
  console.log(e.detail);
  change = {
    id: item.id,
    name: e.detail
  };
  return dispatch("update", change);
};

updateAttribute = function(e) {
  var change, changedTask;
  change = e.detail;
  changedTask = item[collection].find({
    id: change.id
  });
  if (changedTask != null) {
    Object.merge(changedTask, change);
    item[collection] = item[collection];
    return dispatch("update", change);
  } else {
    return console.log("unchanged");
  }
};

update = function() {
  return dispatch("update");
};

add = function(event) {
  var newRecord;
  console.log("add");
  console.log(event.detail);
  newRecord = subDocument.stub(event.detail);
  newRecord.ordinal = (item[collection] || []).length;
  item[collection] = item[collection].add(newRecord);
  return dispatch("update");
};

// drag and drop
dropEvent = function(e) {
  var invalidDrop, refresh, tree;
  refresh = false;
  tree = new HierarchicalList(item[collection]);
  invalidDrop = tree.isInvalidDrop(e.detail);
  if (invalidDrop != null) {
    message = invalidDrop;
    showToast = true;
  } else {
    refresh = tree.dropEvent(e.detail);
  }
  if (refresh) {
    item[collection] = tree.list;
    return dispatch("update");
  }
};

rowEl = void 0;

sort = function(list) {
  if (item[collection] != null) {
    return item[collection].sortBy("ordinal");
  } else {
    return [];
  }
};

getWidth = function(el) {
  if (el != null) {
    return el.clientWidth;
  } else {
    return 0;
  }
};

$: list = sort(item[collection]);

$: show = showToast;
</script>
<style lang="sass">[type="list-item"] {
  width: 100%;
  max-width: 100%; }

.fill {
  fill: var(--gray); }
  .fill:hover {
    fill: var(--blue); }

/*# sourceMappingURL=ClipList.svelte.css.map */</style>

<Section name={collection.titleize()} {open}>

  {#if list && list.length}
    <div>
      <DroppableDivider index={-1} {listId} {sameListOnly} on:drop={dropEvent} />
      {#each list as clipItem, index}
        <DraggableItem item={clipItem} index={index} {listId} {sameListOnly} on:drop={dropEvent} on:update={updateAttribute}>

          <div layout="row" bind:this={rowEl} type="list-item">
            <Indent level={(clipItem.indent || 0).clamp(0,5)}/>
            <div flex="initial"  style="padding-right: 8px">
              <button type="button" icon on:click|preventDefault|stopPropagation={copy(clipItem)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M19,21H8V7H19M19,5H8A2,2 0 0,0 6,7V21A2,2 0 0,0 8,23H19A2,2 0 0,0 21,21V7A2,2 0 0,0 19,5M16,1H4A2,2 0 0,0 2,3V17H4V3H16V1Z" class="fill"/></svg>
              </button>
            </div>
            <div flex ellipsis main>
              <InlineInput bind:value={clipItem.name} on:update={updateName} {style} let:label>
                <label>{ label || "No name"}</label>
              </InlineInput>



                <InlineInput bind:value={clipItem.content} on:update={updateName} {style} let:label>
                  {#if clipItem.is_visible}
                    { label || "No content"}
                  {:else}
                    <span type="hidden">Value is Hidden</span>
                  {/if}
                </InlineInput>




            </div>

          </div>

          <div slot="menu">
            <Menu item={clipItem} on:update={updateAttribute} on:destroy={destroy}/>
          </div>
        </DraggableItem>
        <DroppableDivider item={clipItem} index={index} {listId} {sameListOnly} on:drop={dropEvent} />
      {/each}
    </div>
  {/if}
  <QuickEntry on:add={add}/>

  <Toast bind:show={showToast} {message}/>

</Section>
