  languages: ['javascript', 'ruby', 'python']
});

onMount(function() {
  quill = new Quill(editorRef, {
    modules: {
      // syntax: true
      toolbar: toolbarOptions,
      markdownShortcuts: {},
      table: true
    },
    //tableUI: true
    //magicUrl:
