    //keyboard:
    //  bindings: QuillBetterTable.keyboardBindings
    placeholder: placeholder,
    theme: "snow"
  });
  /*
  quill.keyboard.addBinding
    key: 27 # escape
    handler: (range, context) ->
  dispatch "blur"
  */
  quill.keyboard.addBinding({
    key: 's', // escape
    shortKey: true,
    handler: function(range, context) {
      // console.error "Saave button"
      return dispatch("blur");
    }
  });
  quill.focus();
  return quill.on("editor-change", function(eventName, ...args) {
    var changes, html, text;
    html = editorRef.children[0].innerHTML;
    text = editorRef.children[0].innerText;
    changes = {
      type: "selection-change",
      html: html,
      text: text,
      event: eventName,
      args: args
    };
    return dispatch("change", changes);
  });
});

