<script lang="coffeescript">var add, destroy, dispatch, dropEvent, save, saveName, sort, style, subDocument, update, updateAttribute, updateName;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import SubDocument from "../../../helpers/time_entry.js";

import QuickEntry from "../QuickEntry/TimeEntry.svelte";

import HoverableItem from "../Lists/HoverableItem.svelte";

import Menu from '../Menus/TimeEntry.svelte';

import InlineInput from '../../../components/Form/InlineInput/InlineEffort.svelte';

import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte';

import EffortFormat from '../../../components/Format/EffortFormat.svelte';

import TimeEntryDatePopover from '../Popovers/TimeEntryDate.svelte';

import AssignmentPopover from '../Popovers/Assignment.svelte';

import Section from "./_Section.svelte";

export var assignments = [];

export var collection = "time_entries";

export var item = void 0;

export var listId = uuid();

export var sameListOnly = true;

export var project = void 0;

subDocument = new SubDocument;

onMount(function() {
  switch (collection) {
    case "tasks":
      subDocument = new Task;
      break;
    case "documents":
      subDocument = new Document;
      break;
    case "assignments":
      subDocument = new Assignment;
  }
  // }
  return console.log(item[collection]);
});

updateName = function(e) {
  var change;
  console.log(e.detail);
  change = {
    id: item.id,
    name: e.detail
  };
  return dispatch("update", change);
};

updateAttribute = function(e) {
  var change, changedTask;
  console.log("Time.svelte updateAttribute");
  console.log(e.detail);
  change = e.detail;
  // change["id"] = item.id
  changedTask = item[collection].find({
    id: change.id
  });
  Object.merge(changedTask, change);
  item[collection] = item[collection];
  console.log(change);
  return dispatch("update", change);
};

update = function() {
  return dispatch("update");
};

add = function(event) {
  var newRecord;
  newRecord = subDocument.stub(event.detail);
  newRecord.ordinal = (item[collection] || []).length;
  item[collection] = item[collection].add(newRecord);
  return dispatch("update");
};

saveName = function(e) {
  var change;
  change = {
    id: item.id,
    name: e.detail
  };
  return dispatch("update", change);
};

save = function(e) {
  var change;
  change = e.detail;
  change["id"] = item.id;
  return dispatch("update", change);
};

destroy = function(e) {
  var showDeleteConfirmation;
  console.log("Time.destroy");
  console.log(e.detail);
  item[collection].remove({
    id: e.detail.id
  });
  item[collection] = item[collection];
  dispatch("update");
  return showDeleteConfirmation = false;
};

style = ":hover {font-weight: bold}";

dropEvent = function(e) {
  var dragged, dropped, refresh;
  refresh = true;
  if (e.detail.divider != null) {
    console.log("shift ordinals");
    // up or down?
    if (e.detail.dragIndex !== e.detail.dropIndex) {
      if (e.detail.dropIndex < e.detail.dragIndex) {
        item[collection].forEach(function(item) {
          if (item.ordinal >= e.detail.dropIndex) {
            return item.ordinal = item.ordinal + 1;
          }
        });
        dragged = item[collection].find({
          id: e.detail.dragItem.id
        });
        dragged.ordinal = e.detail.dropIndex;
      } else {
        item[collection].forEach(function(item) {
          if (item.ordinal <= e.detail.dropIndex) {
            return item.ordinal = item.ordinal - 1;
          }
        });
        dragged = item[collection].find({
          id: e.detail.dragItem.id
        });
        dragged.ordinal = e.detail.dropIndex - 1;
      }
    } else {
      refresh = false;
    }
  } else {
    dragged = item[collection].find({
      id: e.detail.dragItem.id
    });
    dropped = item[collection].find({
      id: e.detail.dropItem.id
    });
    dragged.ordinal = e.detail.dropItem.ordinal;
    dropped.ordinal = e.detail.dragItem.ordinal;
  }
  // cleanup ordinals in case there are stragglers
  item[collection].sortBy("ordinal");
  item[collection].forEach(function(item, index) {
    return item.ordinal = index;
  });
  //#console.log "---before"
  //console.log item[collection]
  item[collection] = item[collection];
  //console.log "sorted and fixed"
  //console.log item[collection]
  if (refresh) {
    return dispatch("update");
  }
};

sort = function(list) {
  if (item[collection] != null) {
    return item[collection].sortBy("ordinal");
  } else {
    return [];
  }
};

$: list = sort(item[collection]);

//-- copypasta
</script>
<style lang="sass">svg[state]:hover .fill {
  fill: var(--gray); }

svg[state] .fill {
  fill: var(--gray); }

svg[state="empty"]:hover .fill {
  fill: var(--blue); }

svg[state="empty"] .fill {
  fill: var(--lightest-gray); }

a {
  cursor: pointer; }

[type="list-item"] {
  width: 100%;
  max-width: 100%;
  margin-bottom: 8px; }

.fill {
  fill: var(--gray); }
  .fill:hover {
    fill: var(--blue); }

/*# sourceMappingURL=Time.svelte.css.map */</style>


<Section name={collection.titleize()} {open}>

  {#if list && list.length}
    <div>

      {#each list as timeItem, index}
        <HoverableItem item={timeItem} index={index}>
          <div layout="row" type="list-item">


            <div flex="flex">
              <div layout="row">
                <div flex="initial" section="due-date">
                  <TimeEntryDatePopover item={timeItem} value={timeItem.booking_date} on:update={updateAttribute} on:open on:close/>
                </div>
                <div flex="initial">
                  <a>
                    <em>{Date.create(timeItem.booking_date).dbDate()}
                    </em>
                  </a>
                </div>

                <div flex="initial"><span spacer/></div>
                <div flex="flex" ellipsis>
                  <InlineInput bind:value={timeItem.effort_minutes} on:update={updateAttribute({detail: {id: timeItem.id, effort_minutes: timeItem.effort_minutes}})}  {style} let:label>
                    <EffortFormat value={label}/>
                  </InlineInput>
                </div>
                {#if (timeItem.memo || "").length > 0}
                  <div flex="initial">
                    <Tooltip content="Has content">
                      <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24" {state}><path d="M0 0h24v24H0V0z" fill="none"/><path d="M14 2H4v20h16V8l-6-6zm2 16H8v-2h8v2zm0-4H8v-2h8v2zm-3-5V3.5L18.5 9H13z" class="fill"/></svg>
                    </Tooltip>
                  </div>
                {/if}

              </div>
            </div>
            <div flex="initial">
                {#if (project.assignments.length > 0)}
                  <div flex="initial" present={false}>
                    <AssignmentPopover {project} item={timeItem} value={timeItem.assigned_to_id} on:save={save} on:open on:close/>
                  </div>
                {/if}
            </div>


          </div>
          <div slot="menu">
            <Menu item={timeItem} on:update={updateAttribute} on:destroy={destroy}/>
          </div>
        </HoverableItem>
      {/each}
    </div>
  {/if}

  <QuickEntry on:add={add} stubPlaceholder="Add Time Entry"/>

</Section>
