<script lang="coffeescript">var getProjects, itemsWithDueDates, jump, list, rawList, resource, updateProject;

import PageContent from '../_Common/PageContent.svelte';

import PageHeader from '../_Common/PageHeader.svelte';

import HomeProjectList from "../_Common/Sections/Home_ProjectList.svelte";

import Project from '../../helpers/project.js';

jump = function(anchorName) {
  var elem, scrollContainer;
  elem = u(`[name=${anchorName}]`);
  scrollContainer = u("body > main > section").first();
  return scrollContainer.scrollTop = elem.first().offsetTop - 40;
};

list = [];

rawList = [];

resource = new Project();

getProjects = function() {
  var projectLoadedScb;
  projectLoadedScb = function(projects) {
    list = projects;
    // console.log projects
    return rawList = projects.map(function(p) {
      return Object.select(p, [
        "id",
        "assignments" // this creates a discrete copy of the object. array.clone() does not do that.
      ]);
    });
  };
  return resource.select(projectLoadedScb, null, null);
};

getProjects();

updateProject = function(e) {
  var ecb, scb;
  scb = function(updated) {
    return getProjects();
  };
  ecb = function(err) {
    console.log("udpate err");
    console.log(err);
    return getProjects();
  };
  return resource.updateAttributes(e.detail, scb, ecb, null);
};

itemsWithDueDates = function(projects) {
  var clonedProjects, output;
  output = [];
  clonedProjects = projects.clone();
  output = clonedProjects.filter(function(project) {
    ["tasks", "assignments", "documents"].filter(function(collection) {
      return project[collection] = project[collection].filter(function(collectionItem) {
        //console.log "collectionItme"
        //console.log collectionItem
        collectionItem.checklist_items = collectionItem.checklist_items.filter(function(checklist_item) {
          return checklist_item.due_date != null;
        });
        return (collectionItem.checklist_items.length > 0) || (collectionItem.due_date != null);
      });
    });
    return (project["tasks"].length > 0) || (project["assignments"].length > 0) || (project["documents"].length > 0) || (project.due_date != null);
  });
  return output;
};
</script>

<style lang="sass">ul[slot="nav"] {
  margin-top: 100px;
  padding-left: 32px; }
  ul[slot="nav"] a {
    cursor: pointer; }
  ul[slot="nav"] li {
    padding: 4px 0px; }

header {
  margin-bottom: var(--spacing-m); }
  header h1 {
    font-size: 36px;
    color: var(--darkest-blue); }

section {
  margin: 0px 56px 24px 56px; }

/*# sourceMappingURL=Home.svelte.css.map */</style>

<PageContent>
  <a name="top"></a>
  <PageHeader>Current Work</PageHeader>

  <section>
    <div>
      <HomeProjectList list={itemsWithDueDates(list)} on:update={updateProject} {rawList} on:refresh={getProjects}/>
    </div>
    <p>Current Work is any project, task, document, assignment or check list item that has a due date.</p>
  </section>
</PageContent>
