<script lang="coffeescript">var component, dispatch, getProjects, handleMenuSelection, list, popover, resource, tippyObject, width;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import tippy from 'tippy.js';

import {
  push
} from 'svelte-spa-router';

import Menu from "./_Favorites.svelte";

import Project from "../../../helpers/project.js";

tippyObject = void 0;

component = void 0;

resource = new Project();

list = [];

getProjects = function() {
  var scb;
  scb = function(projects) {
    component.$set({
      list: projects
    });
    console.log("scb");
    return console.log(projects);
  };
  return resource.select(scb, null, null);
};

getProjects();

handleMenuSelection = function(selection) {
  switch (selection) {
    case "collections":
      return push("#/projects");
    default:
      return push(`#/projects/${selection}`);
  }
};

// "
popover = function(element, content) {
  var tippyOptions;
  tippyOptions = {
    distance: '0px',
    theme: "menu",
    appendTo: 'parent',
    trigger: 'click',
    placement: "bottom-end",
    interactive: true,
    popperOptions: {
      modifiers: [
        {
          name: "flip",
          options: {
            fallbackPlacements: ["top",
        "right",
        "left"]
          }
        }
      ]
    }
  };
  if (typeof content === 'function') {
    tippyOptions.onCreate = function(instance) {
      component = new content({
        target: instance.popper.querySelector('.tippy-content')
      });
      component.$on("selected", function(e) {
        tippyObject.hide();
        return handleMenuSelection(e.detail);
      });
      component.$set({
        list: list
      });
    };
  } else {
    tippyOptions.content = content;
  }
  if (Object.keys(tippyOptions).length === 0) {
    tippyObject = tippy(element);
  } else {
    tippyObject = tippy(element, tippyOptions);
  }
  return;
  return {
    update: function(params) {
      console.error("update");
      if (component) {
        component.$set({
          list: list
        });
      }
    },
    destroy: function() {
      tippyObject.destroy();
      if (component) {
        // debugger
        component.$destroy();
      }
    }
  };
};

width = "200px";
</script>
