<script lang="coffeescript">var dispatch, findRawProject, goToItem, hasAssignments, mounted, resource, sort, style, subDocument, update, updateAttribute, updateChecklist;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import HomeChecklist from "./Home_Checklist.svelte";

import QuickEntry from "../QuickEntry/GenericItem.svelte";

import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte';

import Menu from '../Menus/SubDocument.svelte';

import ProgressPopover from '../Popovers/Progress.svelte';

import DueDatePopover from '../Popovers/DueDate.svelte';

import AssignmentPopover from '../Popovers/Assignment.svelte';

import TimePopover from '../Popovers/Time.svelte';

import ChecklistPopover from '../Popovers/Checklist.svelte';

import CommentPopover from '../Popovers/Comment.svelte';

import Section from "./_Section.svelte";

export var assignments = [];

// optional
export var showProgress = true;

export var showAssignment = true;

export var showChecklist = false;

export var collection = "tasks";

import Task from "../../../helpers/task.js";

import Assignment from "../../../helpers/assignment.js";

import Document from "../../../helpers/document.js";

export var item = void 0;

export var project = void 0;

export var rawList = [];

import Project from '../../../helpers/project.js';

resource = new Project();

subDocument = void 0;

mounted = false;

onMount(function() {
  switch (collection) {
    case "tasks":
      subDocument = new Task;
      break;
    case "documents":
      subDocument = new Document;
      break;
    case "assignments":
      subDocument = new Assignment;
  }
  // }
  project[collection].forEach(function(subitem) {
    return subitem = subDocument.upgradeModel(subitem);
  });
  return mounted = true;
});

findRawProject = function(id) {
  var found;
  //console.log "Find  Raw Project #{id}"
  //console.log rawList
  found = rawList.find({
    id: id
  });
  //console.log found
  return found;
};

goToItem = function(childItem) {
  return location.href = `#/projects/${project.id}/${collection}/${childItem.id}`;
};

style = ":hover {font-weight: bold}";

updateAttribute = function(e) {
  var cb, change, changedTask;
  console.log("HomeSubdocumentList updateAttribute");
  console.log(e.detail);
  change = e.detail;
  changedTask = project[collection].find({
    id: change.id
  });
  Object.merge(changedTask, change);
  cb = function(response) {
    console.log("Subdocument updated");
    console.log(response);
    project[collection] = project[collection];
    return dispatch("refresh");
  };
  return resource.updateSubdocument({
    project: project,
    subDocument: changedTask
  }, cb);
};


// note the checklist collection has already been updated before
// this event has been called

updateChecklist = function(e) {
  var cb;
  console.log("Home_SubDocumentList.updateChecklist");
  console.log(e.detail);
  cb = function(response) {
    console.log("checklist item updated");
    console.log(response);
    return dispatch("refresh");
  };
  return resource.updateChecklistItem(e.detail, cb);
};

hasAssignments = function() {
  return true;
};

$: showAssignment = hasAssignments(item);

update = function() {
  return dispatch("update");
};

sort = function(list) {
  if (project[collection] != null) {
    return project[collection].sortBy("ordinal");
  } else {
    return [];
  }
};

$: list = sort(project[collection]);


</script>


{#if mounted}
  <section>

    {#each list as listItem, index}

        <div layout="row" type="list-item">
          {#if showProgress}
            <div flex="initial" section="progress" style="padding-right: 8px">
              <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateAttribute} on:open on:close/>
            </div>
          {/if}
          <div flex ellipsis>
            <InlineInput bind:value={listItem.name} on:update={updateAttribute({ detail: {id: listItem.id, name: listItem.name } })} on:clicked={goToItem(listItem)} {style} let:label>
              <span ellipsis><a href="#/projects/{project.id}/{collection}/{listItem.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></span>
            </InlineInput>
          </div>
          <div flex="initial">
            <div layout="row">



              <!-- due date menu -->
              <div flex="initial" section="due-date" present={listItem.due_date != null}>
                <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateAttribute} on:open on:close/>
              </div>
              <div flex="initial" section="time" present={false}>
                <TimePopover item={listItem} value={listItem.due_date} on:update={updateAttribute} on:open on:close showTimeForm={true}/>
              </div>

              {#if showChecklist}
                {#if (listItem.checklists || []).length > 0}
                <div flex="initial">
                  <ChecklistPopover item={listItem} on:open on:close />
                </div>
                {/if}
              {/if}

              <!-- assignment menu -->
              {#if (listItem.type != "assignment") && (project.assignments.length > 0)}
                <div flex="initial" section="assignment" present={(listItem.assigned_to || []).length > 0}>
                  <AssignmentPopover project={findRawProject(project.id)} bind:item={listItem} on:update={updateAttribute} on:open on:close/>
                </div>
              {/if}

            </div>
          </div>

        </div>
        <div>
        <HomeChecklist project={findRawProject(project.id)} bind:item={listItem} on:update={updateChecklist}/>
        </div>
    {/each}
  </section>
{/if}
<style lang="sass">[type="list-item"] {
  width: 100%;
  margin-bottom: 8px; }
  [type="list-item"] [present], [type="list-item"] [present="false"] {
    display: none; }
  [type="list-item"] [present="true"] {
    display: flex; }
  [type="list-item"]:hover [present] {
    display: flex; }

/*# sourceMappingURL=Home_SubDocumentList.svelte.css.map */</style>
