<script lang="coffeescript">// Project Show
var copyThisUrl, deferredAttributeUpdate, deferredUpdate, expandState, getProject, jump, list, project, refresh, resource, updateProject, updateProjectAttribute;

import {
  push,
  pop,
  replace,
  location
} from 'svelte-spa-router';

import Resource from '../../helpers/project.js';

export var params = {};

import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte';

import FavoritesMenu from "../_Common/Menus/Favorites.svelte";

import Progress from '../_Common/Popovers/Progress.svelte';

import DueDatePopover from '../_Common/Popovers/DueDate.svelte';

import EstimatePopover from '../_Common/Popovers/Time.svelte';

import PageContent from '../_Common/PageContent.svelte';

import PageHeader from '../_Common/PageHeader.svelte';

import Description from '../_Common/Sections/Description.svelte';

import SubDocumentList from '../_Common/Sections/SubDocumentList.svelte';

import ClipList from '../_Common/Sections/ClipList.svelte';

import Reports from './Sections/Reports.svelte';

import Settings from './Sections/Settings.svelte';

resource = new Resource;

project = void 0;

list = [];

getProject = function() {
  var scb;
  scb = function(doc) {
    project = doc;
    console.log("-0-project");
    return console.log(project);
  };
  return resource.selectOne(params.id, scb, null, null);
};

updateProject = function() {
  var ecb, scb;
  scb = function(updated) {
    return console.log("updated");
  };
  ecb = function(err) {
    console.log("udpate err");
    console.log(err);
    console.log("should refresh the proejct");
    return getProject();
  };
  return resource.update(project, scb, ecb, null);
};

deferredUpdate = updateProject.debounce(500);

updateProjectAttribute = function(e) {
  var ecb, scb;
  scb = function(updated) {
    console.log("updated attributes");
    return getProject();
  };
  ecb = function(err) {
    console.log("udpate err");
    console.log(err);
    console.log("should refresh the proejct");
    return getProject();
  };
  return resource.updateAttributes(e.detail, scb, ecb, null);
};

deferredAttributeUpdate = updateProjectAttribute.debounce(500);

jump = function(anchorName) {
  var elem, scrollContainer;
  elem = u(`[name=${anchorName}]`);
  scrollContainer = u("body > main > section").first();
  return scrollContainer.scrollTop = elem.first().offsetTop - 40;
};

refresh = function(e) {
  console.log("should refresh project");
  return resource.selectOne(params.id, scb, null, null);
};

expandState = function(expanded) {
  return ["description", "assignments", "tasks", "documents", "clips", "settings"].forEach(function(key) {
    return project.settings.sections[key].expanded = expanded;
  });
};

copyThisUrl = function(e) {
  var dummy, tempId, url;
  dummy = document.createElement("input");
  // dummy.style.display = 'none'
  url = window.location.href;
  document.body.appendChild(dummy);
  tempId = uuid();
  dummy.setAttribute("id", `clip-${tempId}`);
  document.getElementById(`clip-${tempId}`).value = url;
  dummy.select();
  document.execCommand("copy");
  document.body.removeChild(dummy);
  return console.log(`copied ${window.location.href}`);
};

$: title = project ? project.name : "Unnamed project";

// handle a reroute
$: if (params.id) { getProject() };
</script>

<svelte:head>
  <title>{title}</title>
</svelte:head>
{#if project}

  <PageContent>

    <a name="top"></a>
    <PageHeader>
      <h6 slot="subtitle">
        <FavoritesMenu/>

      </h6>
      <div slot="icon"><Progress bind:value={project.progress} item={project} on:update={updateProjectAttribute} size={36}/></div>
      <InlineInput bind:value={project.name} on:update={deferredUpdate} style="margin-left: -2px" inputStyle="margin-top: 2px; margin-left: -1px"/>
      <div slot="secondary-info">
        <div layout="row">



          <!-- due date menu -->
          <div flex="initial" present={project.due_date != null}>
            <DueDatePopover item={project} value={project.due_date} on:update={updateProjectAttribute} on:open on:close isListItem={false}/>
          </div>
          <div flex="initial" present={(project.estimate || 0) > 0} style="padding-left: 8px">
            <EstimatePopover item={project}  on:update={updateProjectAttribute} on:open on:close isListItem={false}/>
          </div>



        </div>
      </div>
    </PageHeader>



    <a name="about"></a>
    <Description bind:item={project} bind:open={project.settings.sections.about.expanded} on:update={deferredUpdate}/>

    <a name="assignments"></a>
    <SubDocumentList bind:item={project} bind:open={project.settings.sections.assignments.expanded} collection="assignments" on:update={deferredUpdate}/>

    <a name="tasks"></a>
    <SubDocumentList bind:item={project} bind:open={project.settings.sections.tasks.expanded} collection="tasks" on:update={deferredUpdate}/>

    <a name="documents"></a>
    <SubDocumentList bind:item={project} bind:open={project.settings.sections.documents.expanded} collection="documents" on:update={deferredUpdate}/>

    <a name="clips"></a>
    <ClipList bind:item={project} bind:open={project.settings.sections.clips.expanded} collection="clips" on:update={deferredUpdate}/>

    <!--<a name="reports"></a>
    <Reports bind:project={project} bind:open={project.metadata.expanded_sections.reports}/>
    -->

    <a name="settings"></a>
    <Settings bind:item={project} bind:open={project.settings.sections.settings.expanded} on:update={deferredUpdate}/>

  </PageContent>

{/if}
<style lang="sass">h6 {
  text-transform: uppercase;
  margin-bottom: -4px;
  color: var(--gray);
  letter-spacing: 0.75px; }

