<script lang="coffeescript">var checkTypesForListId, closeMenu, dispatch, draggable, draggableEl, drop, enter, getWidth, indent, indentButtonRef, isInvalid, isOver, leave, mousedown, mouseup, over, showIndentMenu, start, toggleIndentMenu;

import {
  Row,
  RowCell
} from "../../../../components/Flex";

import Menu from '../../../../components/Overlay/Menu/ComposedMenu.svelte';

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

export var index = void 0;

export var item = void 0;

export var listId = void 0;

export var indentable = true;

export var sameListOnly = false;

export var showMenu = true;

showIndentMenu = false;

indentButtonRef = void 0;

draggableEl = void 0;

draggable = false;

isOver = false;

isInvalid = false;

checkTypesForListId = function(types) {
  return types.find(function(t) {
    return t.startsWith("listid:");
  });
};

start = function(e) {
  item["listId"] = listId;
  item["index"] = index || null;
  e.dataTransfer.setData("text/plain", JSON.stringify(item));
  return e.dataTransfer.setData(`listid:${listId}`, JSON.stringify(item));
};

over = function(e) {
  var draggedListId;
  e.preventDefault();
  isOver = true;
  if (!!sameListOnly) {
    draggedListId = checkTypesForListId(e.dataTransfer.types);
    if (draggedListId != null) {
      return isInvalid = !(listId === draggedListId.replace("listid:", ""));
    } else {
      return isInvalid = true;
    }
  }
};

leave = function(e) {
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

drop = function(e) {
  var dragItem, dropped;
  isOver = false;
  isInvalid = false;
  draggable = false;
  e.preventDefault();
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  dropped = true;
  if (!!sameListOnly) {
    // console.log "This list: #{listId} :: itemList #{dragItem.listId}"
    dropped = listId === dragItem.listId;
  }
  if (dropped) {
    return dispatch("drop", {
      dragItem: dragItem,
      dragIndex: dragItem.index,
      dragListId: dragItem.listId,
      dropItem: item,
      dropIndex: index,
      dropListId: listId
    });
  }
};

enter = function(e) {
  console.log("etnered");
  return console.log(e);
};

mouseup = function() {
  draggable = false;
  return isInvalid = false;
};

mousedown = function() {
  return draggable = true;
};

getWidth = function(el) {
  if (el != null) {
    return el.clientWidth - 48;
  } else {
    return 0;
  }
};

closeMenu = function() {
  return showIndentMenu = false;
};

indent = function(indentLevel) {
  console.log(`indent ${indentLevel}`);
  dispatch("update", {
    id: item.id,
    indent: indentLevel
  });
  // console.log {id: item.id, indent: indentLevel }
  return closeMenu();
};

toggleIndentMenu = function() {
  return showIndentMenu = true;
};

$: draggableWidth = getWidth(draggableEl) ;
</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragleave={leave}
  bind:this={draggableEl}
  {indentable}

>

      <div flex="initial" handle on:mouseup={mouseup(item)} on:mousedown={mousedown(item)}>
        <slot name="grabber">
          <svg class="handle" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M9,3H11V5H9V3M13,3H15V5H13V3M9,7H11V9H9V7M13,7H15V9H13V7M9,11H11V13H9V11M13,11H15V13H13V11M9,15H11V17H9V15M13,15H15V17H13V15M9,19H11V21H9V19M13,19H15V21H13V19Z"/></svg>
        </slot>
      </div>
      <div flex="initial" indent-menu>
        <button bind:this={indentButtonRef} icon on:click={toggleIndentMenu}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" version="1.1" viewBox="0 0 24 24"><path d="M20,18H22V6H20M11.59,7.41L15.17,11H1V13H15.17L11.59,16.58L13,18L19,12L13,6L11.59,7.41Z"/></svg>
        </button>
        <Menu trigger={indentButtonRef} bind:showMenu={showIndentMenu} align="left" width="189px">
          <ul>
            <li>
              {#each [0,1,2,3,4,5] as indentLevel}
                <button type="button" on:click={indent(indentLevel)} disabled={(item.indent || 0) == indentLevel} indent>{indentLevel}</button>
              {/each}
            </li>
          </ul>
        </Menu>
      </div>
      <div flex class="slot"><slot item={item} index={index}/></div>
      <div flex="initial" draggable-item-menu style="padding-left: 24px;padding-right: 4px">
        <slot name="menu"/>
      </div>

</div>
