<script lang="coffeescript">// todo: implement cancel
var click, clickCount, clickReset, delayedReset, dispatch, double, handleBlur, handleChange, handleInput, handleInputClick, handleKeyup, inputEl, label, originalValue, single;

import {
  tick,
  createEventDispatcher,
  onMount
} from 'svelte';

import DurationInput from "../TranslatedInput/DurationInput.svelte";

dispatch = createEventDispatcher();

// todo: cancel

// Props
export var value = '';

export var style = void 0;

export var inputStyle = void 0;

export var type = 'text';

export var placeholder = '';

export var labelClasses = '';

export var inputClasses = '';

export var doubleClick = true;

export var id = void 0;

export var delay = 250;

export var editing = false;

inputEl = void 0;

label = void 0;

originalValue = void 0;

// Computed
$: isText = type === 'text';

$: isNumber = type === 'number';

$: if (isNumber) {
      label = value === '' ? placeholder : value;
    } else if (isText) {
      label = value ? value : placeholder;
    }
const toggle = async (_) => {
  editing = !editing;
  originalValue = value;
  if (editing) {
    await tick();
    inputEl.focus();
  }
};

onMount(function() {
  return originalValue = value;
});

handleInput = function(e) {
  if (isNumber) {
    value = +e.target.value;
  } else {

  }
  return value = e.target.value;
};

handleKeyup = function(e) {
  // dispatch("keyup", value)
  switch (e.keyCode) {
    case 13:
      // inputEl.blur()
      console.log("should save");
      editing = false;
      return dispatch("update", value);
    case 27:
      value = originalValue;
      editing = false;
      return console.log("should cancel");
  }
};

// }
handleBlur = function(_) {
  var unchanged;
  unchanged = value === originalValue;
  // toggle()
  editing = false;
  if (!unchanged) {
    return dispatch("update", value);
  }
};

// Click tracking
clickCount = 0;

clickReset = function() {
  switch (clickCount) {
    case 1:
      return single();
    case 2:
      return double();
    default:
      return clickCount = 0;
  }
};

// }
delayedReset = clickReset.debounce(delay);

single = function() {
  clickCount = 0;
  if (!doubleClick) {
    toggle();
  }
  return dispatch("clicked", id);
};

double = function() {
  clickCount = 0;
  if (doubleClick) {
    toggle();
  }
  return dispatch("doubleclicked", id);
};

click = function() {
  clickCount++;
  return delayedReset();
};

handleInputClick = function(e) {
  return e.preventDefault;
};

handleChange = function(e) {
  console.log("handleChange");
  return value = e.detail.inMinutes;
};
</script>
<style lang="sass">input, div, div span {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  border: 0px solid transparent;
  padding: 0px; }

input {
  color: var(--dark-red); }

div {
  min-height: var(--font-xl);
  border: 1px solid transparent; }

/*# sourceMappingURL=InlineEffort.svelte.css.map */</style>
{#if editing && (isText || isNumber)}
  <DurationInput
    value={value / 60}
    isMinutes={true}
    on:change={handleChange}
    on:input={handleInput}
    on:keyup={handleKeyup}
    on:blur={handleBlur}
    on:click={handleInputClick}
    />

{:else}
  <div
    {id}
    class={labelClasses}
    ellipsis {style}>
    <span on:click={click} ellipsis><slot {label}><span ellipsis>{label}<span spacer/></span></slot></span>
  </div>
{/if}
